import React, {useEffect, useState} from "react";
import styles from "./verificationMessage.module.css";
import Button from "../../components/button/Button";
import { client } from "../../api/client";
import { GET_USER_BY_ID } from "../../api/operations/queries/users";

const VerificationMessage = () => {
  const [userName, setUserName] = useState('')
  const getUser = async () => {
    const { data, loading, error } = await client.query({
      query: GET_USER_BY_ID,
      variables: {
        id: Number(sessionStorage.getItem("userId")),
      },
    });
    setUserName(data.user.firstName)
  };
  useEffect(() => {
    getUser();
  });
  return (
    <div className={styles.verificationMessage}>
      <div className={styles.verificationMessageBody}>
        <h2>{userName}, !   !</h2>
        <p>      .</p>
        <p>
                   
          .
        </p>
        <p>
            ,     .
        </p>
        <h1>
           : <span>{sessionStorage.getItem("userId")}</span>
        </h1>
        <p>   .</p>
        <Button onClick={()=>sessionStorage.removeItem('userId')}>  </Button>
      </div>
    </div>
  );
};

export default VerificationMessage;
